import React, { useState, useEffect, useCallback } from "react";
import { useVersion, useDataProvider } from 'react-admin';
import { Card, CardHeader, useMediaQuery } from "@material-ui/core";
import Admins from "./Admins";
import Applications from "./Applications";
import Organizations from "./Organizations";
import useAdminProvider from "../../hooks/useAdminProvider";

const styles = {
  flex: { display: 'flex' },
  flexColumn: { display: 'flex', flexDirection: 'column' },
  leftCol: { flex: 1, marginRight: '0.5em' },
  rightCol: { flex: 1, marginLeft: '0.5em' },
  singleCol: { marginTop: '1em', marginBottom: '1em' },
};

const Spacer = () => <span style={{ width: '1em' }} />;
const VerticalSpacer = () => <span style={{ height: '1em' }} />;

const Dashboard = () => {
  const [state, setState] = useState({});
  const [adminCount] = useAdminProvider();
  const version = useVersion();
  const dataProvider = useDataProvider();
  const isXSmall = useMediaQuery((theme) =>
    theme.breakpoints.down('xs')
  );
  const isSmall = useMediaQuery((theme) =>
    theme.breakpoints.down('md')
  );

  const fetchAdminCount = useCallback(async () => {
    const { total } = await dataProvider.getList("Admins");
    setState(state => ({
      ...state,
      admins: total,
    }));
  }, [dataProvider]);

  const fetchApplicationCount = useCallback(async () => {
    const { total } = await dataProvider.getList("Applications");
    setState(state => ({
      ...state,
      applications: total,
    }));
  }, [dataProvider]);

  const fetchOrganizationCount = useCallback(async () => {
    const { total } = await dataProvider.getList("Organizations");
    setState(state => ({
      ...state,
      organizations: total,
    }));
  }, [dataProvider]);

  return (
    <Card>
      <CardHeader title="Welcome to Admin Dashboard" />
      <Admins count={adminCount} />
    </Card>
  );
};

export default Dashboard;
